:root{
    --color-gold-dark: rgb(214, 176, 116);
    --color-gold-normal: rgb(246, 194, 78);
    --color-gold-light: rgb(246, 224, 189);

    --color-black-dark: rgb(25, 25, 25);
    --color-black-normal: rgb(45, 47, 51);
    --color-black-light: rgb(58, 61, 70);

    --color-grey-dark: rgb(147, 151, 167);
    --color-grey-normal: rgb(205, 206, 214);
    --color-grey-light: rgb(228, 228, 229);

    --color-white-dark: rgb(225, 225, 225);
    --color-white-normal: rgb(251, 251, 251);
    --color-white-light: rgb(255, 255, 255);

    --color-blue-dark: rgb(17, 42, 149);
    --color-blue-normal: rgb(21, 61, 232);
    --color-blue-light: rgb(116, 142, 254);

    --color-green-dark: rgb(87, 141, 54);
    --color-green-normal: rgb(105, 179, 60);
    --color-green-light: rgb(135, 229, 78);

    --color-red-dark: rgb(173, 36, 71);
    --color-red-normal: rgb(206, 21, 68);
    --color-red-light: rgb(245, 13, 71);

    --color-cyan-dark: rgb(19, 127, 134);
    --color-cyan-normal: rgb(29, 165, 175);
    --color-cyan-light: rgb(21, 212, 226);


    /* Below colors will have alpha channel */
    --color-cyan-transparent: rgba(4, 160, 182, 0.75);
}

body, body *{
    margin: 0;
    text-decoration: none;
    box-sizing: border-box !important;
    line-height: 1.5 !important;
    -webkit-tap-highlight-color: transparent;
    outline: none !important;
}

p{
    text-align: center;
}

.GeneralTitle{
    white-space: nowrap;
    margin: 0.5rem;
    font-size: var(--font-size-extra);
    font-weight: 500;
    color: var(--color-blue-dark);
    overflow: hidden;
    -webkit-animation: TitleScrollAnimation 6s forwards;
            animation: TitleScrollAnimation 6s forwards;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
