@-webkit-keyframes FadeIn{
    0%{opacity: 0;} 100%{opacity: 1;}
}

@keyframes FadeIn{
    0%{opacity: 0;} 100%{opacity: 1;}
}

@-webkit-keyframes TitleScrollAnimation{
    0%{opacity: 0; max-width: 0;} 
    100%{opacity: 1; max-width: 30rem;}
}

@keyframes TitleScrollAnimation{
    0%{opacity: 0; max-width: 0;} 
    100%{opacity: 1; max-width: 30rem;}
}

@-webkit-keyframes PopUpFromBottom{
    0%{-webkit-transform: translateY(100vh);transform: translateY(100vh); opacity: 0;}
    100%{-webkit-transform: translateY(0);transform: translateY(0); opacity: 1;}
}

@keyframes PopUpFromBottom{
    0%{-webkit-transform: translateY(100vh);transform: translateY(100vh); opacity: 0;}
    100%{-webkit-transform: translateY(0);transform: translateY(0); opacity: 1;}
}

@-webkit-keyframes HorinzontalShaking{
    0%{-webkit-transform: translateX(-20%);transform: translateX(-20%)}
    25%{-webkit-transform: translateX(20%);transform: translateX(20%)}
    50%{-webkit-transform: translateX(-10%);transform: translateX(-10%)}
    75%{-webkit-transform: translateX(10%);transform: translateX(10%)}
    100%{-webkit-transform: translateX(0%);transform: translateX(0%)}
}

@keyframes HorinzontalShaking{
    0%{-webkit-transform: translateX(-20%);transform: translateX(-20%)}
    25%{-webkit-transform: translateX(20%);transform: translateX(20%)}
    50%{-webkit-transform: translateX(-10%);transform: translateX(-10%)}
    75%{-webkit-transform: translateX(10%);transform: translateX(10%)}
    100%{-webkit-transform: translateX(0%);transform: translateX(0%)}
}

@-webkit-keyframes TextShinning{
    0%{-webkit-filter:brightness(0.75);filter:brightness(0.75)}
    100%{-webkit-filter:brightness(1.25);filter:brightness(1.25)}
}

@keyframes TextShinning{
    0%{-webkit-filter:brightness(0.75);filter:brightness(0.75)}
    100%{-webkit-filter:brightness(1.25);filter:brightness(1.25)}
}
