button{
    font-size: var(--font-size-normal);
    line-height: 1.25;
    font-weight: 400;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    text-transform: uppercase;
    border-radius: 0.375rem;
    letter-spacing: 0.125rem;
    box-shadow: 0px 0px 0.5rem 0 var(--color-grey-dark);
    text-transform: uppercase;
}
button:hover{
    cursor: pointer;
    outline: none;
}
button:focus{
    outline: none;
}
button[disabled]:hover{
    cursor: not-allowed;
}
button.button-1{
    margin: 0.5rem 0rem;
    padding: 0.5rem 1.25rem;
    background-color: var(--color-gold-dark);
    border: 0.1rem solid var(--color-gold-dark);
    color: var(--color-white-normal);
}
button.button-1:hover{
    border-color: var(--color-gold-light);
    color: var(--color-white-light);
    box-shadow: 0px 0px 1.5rem 0 var(--color-grey-dark);
}
button.button-2{
    margin: 0.5rem 0rem;
    padding: 0.5rem 1.25rem;
    background-color: var(--color-grey-normal);
    border: 0.1rem solid var(--color-white-dark);
    color: var(--color-black-normal);
}
button.button-2:hover{
    border-color: var(--color-white-light);
    color: var(--color-black-light);
    box-shadow: 0px 0px 1.5rem 0 var(--color-grey-dark);
}
button.disabled{
    margin: 0.5rem 0rem;
    padding: 0.5rem 1.25rem;
    background-color: var(--color-grey-dark);
    border: 0.1rem solid var(--color-grey-normal);
    color: var(--color-white-dark);
}
button.disabled:hover{
    cursor: not-allowed;
}
form{
    width: 100%;
}
form>div.Inline-Input{
    width: 100%;
    max-width: 20rem;
    margin: 1.25rem auto;
    border: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
}
form>div.Inline-Input>label{
    width: 25%;
    max-width: 2.5rem;
}
form>div.Inline-Input>input{
    width: 75%;
    min-width: calc(100% - 2.5rem);
}
form>div.Inline-Input>span.Symbol{
    position: absolute;
    right: 0.5rem;
    color: var(--color-grey-normal);
    height: 100%;
    display: flex;
    align-items: center;
}
form>div.Inline-Input>span.Symbol:hover{
    cursor: pointer;
}
form>div.Error-Input,
form>div.Success-Input{
    text-align: center;
}
form>div.Error-Input>span,
form>div.Success-Input>span{
    font-size: var(--font-size-small);
    font-weight: 500;
    display: block;
    width: 100%;
    max-width: 20rem;
    margin: 1.25rem auto;
    text-transform: capitalize;
    padding: 0.25rem 0.5rem;
    border-radius: 0.375rem;
    color: var(--color-white-light);
}   
form>div.Error-Input>span{
    background-color: var(--color-red-light);
}
form>div.Success-Input>span{
    background-color: var(--color-green-normal);
}
input{
    -webkit-transition: all 0.75s;
    transition: all 0.75s;
}

input[type='text'],input[type='password'],input[type='number'],input[type='email']{
    border-radius: 0.25rem;
    background-color: transparent;
    border: 1px solid var(--color-white-light);
    box-shadow: 0 0 0.25rem 0 var(--color-white-light);
    padding: 0.2rem 2rem 0.25rem 0.5rem;
    color: var(--color-white-light);
    font-weight: 500;
    font-size: var(--font-size-normal);
}

input[type='text']:focus,input[type='password']:focus,input[type='number']:focus,input[type='email']:focus{
    outline: none;
}

input[type='text']::-webkit-input-placeholder,input[type='password']::-webkit-input-placeholder,input[type='number']::-webkit-input-placeholder,input[type='email']::-webkit-input-placeholder{
    color: var(--color-white-dark);
}

input[type='text']::-ms-input-placeholder,input[type='password']::-ms-input-placeholder,input[type='number']::-ms-input-placeholder,input[type='email']::-ms-input-placeholder{
    color: var(--color-white-dark);
}

input[type='text']::placeholder,input[type='password']::placeholder,input[type='number']::placeholder,input[type='email']::placeholder{
    color: var(--color-white-dark);
}

input[type='text'].Checked:invalid,
input[type='password'].Checked:invalid,
input[type='number'].Checked:invalid,
input[type='email'].Checked:invalid{
    border: 2.5px solid var(--color-red-normal);
    box-shadow: 0 0 0.25rem 0 var(--color-red-light);
    color: var(--color-red-light);
}
:root{
    --font-size-tiny: 0.6rem;
    --font-size-small: 0.85rem;
    --font-size-normal: 1rem;
    --font-size-large: 1.25rem;
    --font-size-extra: 1.5rem;
    --font-size-super: 1.75rem;
}
body{
    font-family: 'Montserrat', sans-serif;
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: -webkit-linear-gradient(left, var(--color-black-dark), var(--color-grey-dark) 50%, var(--color-black-dark));
    background: linear-gradient(to right, var(--color-black-dark), var(--color-grey-dark) 50%, var(--color-black-dark));
    font-weight: 400;
    font-size: var(--font-size-normal);
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently */
}
body>div.scroll-content{
    display: flex;
    justify-content: center;
}
#root{
    width: 100vw;
    min-height: 100vh;
    max-width: calc(576px + calc((100vw - 576px) * 0.2));
    background: -webkit-linear-gradient(var(--color-white-normal), var(--color-white-light));
    background: linear-gradient(var(--color-white-normal), var(--color-white-light));
    position: relative;
    -webkit-transition: height 0.75s;
    transition: height 0.75s;
}

/*--------------    Custom Scrollbar   -------------*/
div.scrollbar-track-y{
    background-color: var(--color-black-dark);
    border-radius: 0.25rem;
}
div.scrollbar-thumb{
    right: 0;
    background-color: var(--color-gold-light);
    border-radius: 0.25rem;
}
div.scrollbar-thumb:hover{
    cursor: pointer;
}

/* Slider Fixing */
.slick-dots{
    bottom: 0px !important;
}


/* Browsers' Default Styling Fixing */
@-webkit-keyframes autofill {
    to {
        color: var(--color-white-light);
        background: transparent;
    }
}
input:-webkit-autofill {
    -webkit-animation-name: autofill;
    -webkit-animation-fill-mode: both;
}
#Compatibility{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 100vh;
    background-color: rgb(246, 224, 189);
    color: rgb(45, 47, 51);
    padding: 1.25rem 0.75rem;
    -webkit-animation: FadeIn 1.75s;
            animation: FadeIn 1.75s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
#Compatibility>p.Title{
    color: rgb(17, 42, 149);
}
#Compatibility>div.Content,
#Compatibility>div.Content>p{
    width: 100%;
}
#Compatibility>div.Content>p:first-child>i{
    font-size: 1.25rem;
    color: rgb(206, 21, 68);
}
#Compatibility>div.Content>p:nth-child(3){
    margin-bottom: 1rem;
}
#Compatibility>div.Content>div.List{
    width: 100%;
    padding: 0 10%;
}
#Compatibility>div.Content>div.List>p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.75rem;
}
#Compatibility>div.Content>div.List>p>a{
    color: rgb(15, 49, 143);
}
#LOGO{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#LOGO>img{
    height: 3rem;
}
#LOGO>img:first-child{
    -webkit-transform: translateX(-50vw);
            transform: translateX(-50vw);
    -webkit-animation: LogoLeft 2s ease-out 0.5s forwards;
            animation: LogoLeft 2s ease-out 0.5s forwards;
}
#LOGO>img:last-child{
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
    -webkit-animation: LogoRight 2s ease-out 0.5s forwards;
            animation: LogoRight 2s ease-out 0.5s forwards;
}
@-webkit-keyframes LogoLeft{
    0%{-webkit-transform: translateX(-50vw);transform: translateX(-50vw);}
    100%{-webkit-transform: translateX(25%);transform: translateX(25%);}
}
@keyframes LogoLeft{
    0%{-webkit-transform: translateX(-50vw);transform: translateX(-50vw);}
    100%{-webkit-transform: translateX(25%);transform: translateX(25%);}
}
@-webkit-keyframes LogoRight{
    0%{-webkit-transform: translateX(50vw);transform: translateX(50vw);}
    100%{-webkit-transform: translateX(-25%);transform: translateX(-25%);}
}
@keyframes LogoRight{
    0%{-webkit-transform: translateX(50vw);transform: translateX(50vw);}
    100%{-webkit-transform: translateX(-25%);transform: translateX(-25%);}
}

div.LoadingScreen{
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-super);
    font-weight: 600;
    z-index: 2;
    background: -webkit-linear-gradient(var(--color-grey-normal), var(--color-grey-dark) 50%, var(--color-grey-normal));
    background: linear-gradient(var(--color-grey-normal), var(--color-grey-dark) 50%, var(--color-grey-normal));
    color: var(--color-gold-dark);
}
div.LoadingScreen>span{
    margin-right: 1.5rem;
}


