#LOGO{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#LOGO>img{
    height: 3rem;
}
#LOGO>img:first-child{
    transform: translateX(-50vw);
    animation: LogoLeft 2s ease-out 0.5s forwards;
}
#LOGO>img:last-child{
    transform: translateX(50vw);
    animation: LogoRight 2s ease-out 0.5s forwards;
}
@keyframes LogoLeft{
    0%{transform: translateX(-50vw);}
    100%{transform: translateX(25%);}
}
@keyframes LogoRight{
    0%{transform: translateX(50vw);}
    100%{transform: translateX(-25%);}
}